import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: () => import('@/pages/Home')
    },
    {
      path: '/announce',
      name: 'Announce',
      component: () => import('@/pages/Announce')
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    // 
    return { x: 0, y: 0 }
  }
})



